@import url("https://fonts.googleapis.com/css2?family=Alata&family=Josefin+Sans:ital,wght@0,300;1,300&display=swap");
@tailwind base;
@tailwind components;
@tailwind utilities;
* {
  margin: 0;
  box-sizing: border-box;
}

h1, h2, h3 {
  @apply font-josefin-sans uppercase text-black text-[28px] leading-none;
}

body {
  @apply text-[15px] text-darkGray;
}

.container {
  @apply px-[30px] lg:px-5;
}

.btn {
  @apply min-w-[200px] border border-black p-4 text-center text-black uppercase tracking-[0.5em] transition-colors;
}
.btn:hover {
  @apply bg-black text-white;
}

.header-fixed {
  @apply absolute w-full pt-[50px] lg:pt-[60px] transition-colors;
}
.header-fixed .main-menu {
  @apply hidden lg:flex w-full lg:flex-initial lg:basis-0;
}
.header-fixed .main-menu-wrap {
  @apply lg:flex lg:gap-8 lg:justify-end;
}
.header-fixed .mobile-menu-hamburger .menu-close {
  @apply hidden;
}
.header-fixed .mobile-menu-hamburger .menu-close.active {
  @apply block;
}
.header-fixed .mobile-menu-hamburger .menu-open.hide {
  @apply hidden;
}
.header-fixed.active {
  @apply bg-black fixed h-screen z-10;
}
.header-fixed.active .main-menu {
  @apply flex items-center;
  height: calc(100vh - 82px);
}

.menu-item {
  @apply mb-12 lg:mb-0;
}
.menu-item a {
  @apply text-[24px] text-white font-josefin-sans uppercase leading-none lg:normal-case lg:font-sans lg:text-[18px];
}
.menu-item:last-of-type {
  @apply mb-0;
}

.nav-wrap {
  @apply flex justify-between items-center flex-wrap;
}

.banner {
  @apply h-screen lg:pb-[145px];
}
.banner .banner-img {
  @apply absolute h-full w-full object-cover;
  z-index: -1;
}
.banner .container {
  @apply h-screen flex items-center;
}
.banner .title-wrap {
  @apply border-2 border-white py-8 px-[34px] w-full lg:max-w-[653px] lg:px-11 lg:py-10;
}
.banner .title-wrap h1 {
  @apply font-josefin-sans uppercase text-white leading-none text-[35px] lg:text-[62px];
}
@media (max-width: 374px) {
  .banner .title-wrap h1 {
    @apply text-[27px];
  }
}

.two-col-image {
  @apply pt-28 pb-16;
}
.two-col-image .text-wrap {
  @apply text-center pt-16 px-2.5;
}
.two-col-image .text-wrap h2 {
  @apply pb-5;
}

.cards {
  @apply pt-16 pb-32;
}
.cards .title-wrap {
  @apply text-center lg:flex lg:justify-between lg:items-center lg:text-left pb-14;
}
.cards .cards-wrap {
  @apply grid lg:grid-cols-4 gap-[30px];
}
.cards .card-item {
  @apply relative;
}
.cards .card-item img {
  @apply absolute w-full h-full;
  z-index: -1;
}
.cards .card-item span {
  @apply block pb-8 pt-16 px-7 text-[29px] font-josefin-sans text-white uppercase max-w-[215px] leading-none relative;
}
.cards .card-item:before {
  @apply absolute w-full h-full z-0;
  content: "";
  background: linear-gradient(90deg, rgba(0, 0, 0, 0.4990589986) 0%, rgba(0, 0, 0, 0) 100%);
}
.cards .card-item:hover span {
  @apply text-black;
}
.cards .card-item:hover::before {
  background: rgba(255, 255, 255, 0.5);
}
.cards .btn-mobile-wrap {
  @apply text-center mt-10;
}

footer {
  @apply bg-black py-16 text-center;
}
footer .col-left img {
  @apply mx-auto mb-11;
}
footer .menu-item {
  @apply mb-7;
}
footer .menu-item a {
  @apply normal-case text-base font-sans;
}
footer .social-wrap {
  @apply flex justify-center gap-5 items-center mt-14 mb-7;
}
footer .copyright {
  @apply text-veryDarkGray;
}